@import url(https://maxcdn.bootstrapcdn.com/bootstrap/4.4.1/css/bootstrap.min.css);
html,body,#root{margin:0;padding:0;height:100%}.main{position:relative;height:100%}.main .aside{max-height:100%;overflow-y:scroll}.main .middlePanel{max-height:100%}

.channelInfo{position:relative;height:5%;max-height:5%;border:1px solid black}.channelInfo .row{height:100%}.channelInfo .row>div{display:flex;align-items:center;justify-content:center}.channelInfo .row>div .link{cursor:pointer;-webkit-user-select:none;-ms-user-select:none;user-select:none}.channelInfo .badge{transition:0.3s}.channelInfo .badge:hover{background-color:#273c75d5}.chatField{position:relative;height:calc(85% - 2%);max-height:calc(85% - 2%);border:2px solid black;margin-top:2%;overflow:scroll}.messageField{position:relative;height:10%;max-height:10%}.messageField .messageInputBox{position:absolute;width:100%}.toast-header{cursor:default}.toast-header button{display:none}

.formControlMessage{border:1px solid black}.formControlMessage:focus{outline:none;box-shadow:none;border:1px solid rgba(0,0,0,0.253)}.sendMessageButton{cursor:pointer}.sendMessageButton span:hover{background-color:#7f8fa66e}

.hideScrollBar{-ms-overflow-style:none;scrollbar-width:none}.hideScrollBar::-webkit-scrollbar{display:none}.chatRoom{cursor:pointer;transition:0.5s;background-color:#dcdde14f;min-height:10%;display:flex;text-align:center;align-content:center;overflow:hidden}.chatRoom:hover{background-color:#dcdde1}.createRoom,.connectToRoom{cursor:pointer;-webkit-user-select:none;-ms-user-select:none;user-select:none;display:flex;text-align:center;font-weight:600}.createRoom .card-header,.connectToRoom .card-header{transition:0.25s}.createRoom .card-header:hover,.connectToRoom .card-header:hover{background-color:#44bd3265}.connectToRoom .card-header{transition:0.25s}.connectToRoom .card-header:hover{background-color:#3257bd65}.lineBorder{height:20px;background-color:rgba(0,0,0,0.616);border-radius:2px}#channelWrapper{height:100%;width:100%}

.panelHeader{display:flex;text-align:center;align-content:center;margin-top:20%}.usersPanel{height:70%}.usersPanel>div{margin-top:5%}.usersPanel li{overflow:hidden;white-space:nowrap}.streamPanel{height:15%}

#authCard{width:100%;box-shadow:0 0 2px 2px rgba(0,0,0,0.658)}#authCard .buttonRow>div{display:flex;justify-content:center}

